import React, { Component } from 'react';
import '../App.css';
import WeatherContainer from '../containers/WeatherContainer';
import Footer from './Footer';
import LocationButton from './LocationButton';
import RefreshButton from './RefreshButton';

class App extends Component {
  constructor(props) {
    super(props);
  
    this.handleGetLocation = this.handleGetLocation.bind(this);
    this.handleRefresh = this.handleRefresh.bind(this);
  }

  componentDidMount() {
    this.fetchData();
  }

  fetchData() {
    const {
      weatherData,
      fetchWeatherSmhi,
      fetchWeatherOwm,
      fetchWeatherDs,
      clearOldData
    } = this.props;
    console.log('KLCIK?');
    const lat = localStorage.getItem('lat') || '59.3293'; // Stockholm as default.
    const lon = localStorage.getItem('lon') || '18.0686'; // Stockholm as default.
    
    // BEHlVS EJ ?
    const appHasOldData = weatherData.combo.status !== null;    
    // console.log(weatherData.combo.status);

    console.log(lat);

    if (appHasOldData) {
      console.log('CLEAR DATA!');
      //clearOldData();
    } else {
      console.log('no data to clear...');
    }
    
    clearOldData();

    fetchWeatherSmhi(lat, lon);
    fetchWeatherOwm(lat, lon);
    fetchWeatherDs(lat, lon);
  }

  handleGetLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        position => {
          const lat = position.coords.latitude.toString();
          const lon = position.coords.longitude.toString();

          localStorage.setItem('lat', lat);
          localStorage.setItem('lon', lon);

          this.fetchData();
        },
        () => { this.fetchData(); }
      );
    } else {
      // No browser support for geolocation.
      this.fetchData();
    }
  }

  handleRefresh() {
    this.fetchData();
  }

  render() {
    return (
      <div className="App">
        <WeatherContainer />

        <div className="button-container">
          <LocationButton onGetLocation={this.handleGetLocation} />
          <RefreshButton onRefresh={this.handleRefresh} />
        </div>

        <Footer />
      </div>
    );
  }
}

export default App;



